"use client";

import { createClient } from "@/lib/supabase/client";

const supabase = createClient();

// ========== OWNED PACKAGES ==========

export type OwnedPackageRow = {
  package_id: string;
  title: string;
  description: string | null;
  created_at: string;
};

export async function getUserOwnedPackages(userId: string) {
  // Thanks to RLS, user only sees packages they own or are owner of.
  const { data, error } = await supabase
    .from("packages")
    .select("package_id, title, description, created_at")
    .order("created_at", { ascending: false });

  return { data: data as OwnedPackageRow[] | null, error };
}

// ========== BILLING / SUBSCRIPTION ==========

export type BillingRow = {
  billing_id: string;
  user_id: string;
  billing_type: "subscription" | "credit";
  amount: number;
  credit_value: number | null;
  notes: string | null;
  created_at: string;
};

// Latest subscription row for the user
export async function getUserSubscription(userId: string) {
  const { data, error } = await supabase
    .from("billing")
    .select("*")
    .eq("billing_type", "subscription")
    .order("created_at", { ascending: false })
    .limit(1)
    .maybeSingle();

  return { data: data as BillingRow | null, error };
}

// Sum of all credits for the user
export async function getUserCredits(userId: string) {
  const { data, error } = await supabase
    .from("billing")
    .select("credit_value")
    .eq("billing_type", "credit");

  const balance =
    data?.reduce((sum, row) => sum + (row.credit_value ?? 0), 0) ?? 0;

  return { balance, error };
}
